/* @import url('http://fonts.cdnfonts.com/css/segoe-ui-4'); */
html {
  scroll-behavior: smooth;
}

.Header1 {
  display: block;
  background: url(./resources/open-book.jpg), rgba(0, 0, 0, 0.21);
  background-blend-mode: darken;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  z-index: -2;
  position: fixed;
  min-height: 100%;
  width: 100%;
  /* top: 0; */
  /* left: 0; */
}

.poster {
  display: flex;
  padding: 1em 2em;
}

.social-logos {
  display: none;
}

.social-logos>a {
  margin: 0.7em;
}

.social-logos>a>img:hover {
  cursor: pointer;
  transition: 0.4s;
  transform: scale(1.2);
}

.poster>a>img {
  width: 50px;
  height: 50px;
}

.social-logos>a>img {
  width: 50px;
  height: 50px;
}

.Navbar {
  padding: 1em;
  background-color: rgb(240, 255, 255, 0.5);
}

.desktop-Navbar {
  display: none;
}

.mobile-Navbar {
  display: block;
  transition: 0.7s;
}

.mobile-Navbar p {
  color: white;
  font-size: large;
  margin-left: 1em;
  width: fit-content;
  transition: 0.7s;
}

.mobile-Navbar p:hover {
  cursor: pointer;
  color: green;
  transition: 0.7s;
  transform: scale(1.1);
}

.mobile-Navbar>span {
  display: block;
  position: relative;
  animation: navSlider 0.7s forwards;
}

@-webkit-keyframes navSlider {
  0% {
    bottom: 50px;
    opacity: 0;
  }

  100% {
    bottom: 0px;
    opacity: 1;
  }
}

@-moz-keyframes navSlider {
  0% {
    bottom: 50px;
    opacity: 0;
  }

  100% {
    bottom: 0px;
    opacity: 1;
  }
}

@-o-keyframes navSlider {
  0% {
    bottom: 50px;
    opacity: 0;
  }

  100% {
    bottom: 0px;
    opacity: 1;
  }
}

@keyframes navSlider {
  0% {
    bottom: 50px;
    opacity: 0;
  }

  100% {
    bottom: 0px;
    opacity: 1;
  }
}

.navlist>a {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: white;
  margin: 2em;
  transition: 0.7s;
}

.navlist>a:hover {
  transition: 0.7s;
  color: green;
}

.Navbar>nav>a {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: white;
  margin: 2em;
  text-decoration: none;
  color: white;
  transition: 0.7s;
  font-size: large;
}

.Navbar>nav>a:hover {
  transition: 0.7s;
  color: green;
}

.Herotext {
  display: block;
  color: white;
  margin: 3em 1em 1em 1em;
  width: 95vw;
}

.Herotext>h1 {
  font-weight: 600;
  color: #fff;
  font-size: 40px;
  line-height: 1.2;
}

.textSpan {
  font-weight: 700;
  color: rgba(255, 255, 255, 0.7);
  font-size: 14px;
  display: inline-block;
  letter-spacing: 2px;
  margin-bottom: 5px;
  position: relative;
  z-index: 0;
}

.Herotext>button {
  padding: 1em 2em;
  font-weight: 600;
  font-size: large;
  border: none;
  width: 92vw;
  border-radius: 5px;
  transition: 0.5s;
}

.Herotext>button:hover {
  border: none;
  background-color: green;
  box-shadow: 2px 2px 20px grey;
  color: white;
  transition: 0.5s;
  cursor: pointer;
}

.UploadForm {
  display: flex;
  z-index: 1;
  background-color: white;
  border-radius: 10px;
  padding: 2em 1em;
  margin: 3em 1em;
  box-shadow: 2px 2px 20px grey;
}

.UploadForm>form>label {
  color: green;
  font-weight: 500;
  font-size: larger;
  margin: 0.5em 1em;
  width: 100vw;
}

.UploadForm>form>input {
  margin: 0.5em 1em;
  padding: 0.5em 0.5em 0 0;
  font-size: larger;
  width: 70vw;
  border: none;
  border-bottom: green solid 2px;
}

.UploadForm>form>input:focus {
  outline: none;
}

.UploadForm>form>a {
  padding: .7em 2em;
  font-weight: 600;
  margin: .4em 1em;
  font-size: large;
  background-color: white;
  border: grey 2px solid;
  text-decoration: none;
  color: black;
  border-radius: 5px;
  transition: 0.5s;

}

.UploadForm>form>a:hover {
  border: none;
  background-color: green;
  box-shadow: 2px 2px 20px grey;
  color: white;
  transition: 0.5s;
  cursor: pointer;
  border: grey solid 2px;
}

.Benifits {
  display: flex;
  z-index: 1;
  background-color: white;
  padding: 1em;
}

.benifits-img {
  display: none;
}

.benifits-content {
  padding: 0.2em;
}

.benifits-content span {
  color: green;
  font-weight: 700;
}

.benifits-content>div {
  display: flex;
}

.benifits-content img {
  margin: 2em 0.5em;
  width: 50px;
  height: 50px;
}

.Figures {
  background: url(./resources/books-figure-section.jpg), rgba(0, 0, 0, 0.2);
  background-blend-mode: darken;
  padding: 1em;
  color: white;
}

.figuresData {
  display: flex;
  /* justify-content: center; */
  margin: 1em;
}

.figuresData img {
  margin: 1em;
}

.figuresData h1 {
  font-weight: 700;
}

.figuresData p {
  margin-top: -1em;
  font-weight: 500;
}

.Courses {
  background-color: white;
  text-align: center;
  padding: 1em 0em;
}

.Courses span {
  color: green;
  font-weight: 700;
  font-size: x-large;
  display: block;
  margin: 2em;
}

.Courses h2 {
  font-size: xxx-large;
  /* margin: 0 0em; */
}

.course-item-container {
  display: grid;
  grid-template-columns: auto;
}

.course-item-container div {
  margin: 0.3em 0.5em;
}

.course-item {
  background-color: rgb(246, 246, 246);
  padding: 3em 1em;
  border-radius: 5px;
  transition: 0.7s;
  font-size: larger;
}

.course-item:hover {
  background-color: green;
  transition: 0.7s;
  color: white;
  cursor: pointer;
}

.Testimonials {
  background-color: white;
  padding: 4em 1em;
  text-align: center;
}

.spanTestimonials {
  font-weight: 700;
  font-size: x-large;
  color: green;
  margin: 1em 0;
  display: block;
}

.Testimonials h2 {
  font-size: xxx-large;
  margin: 0 0.5em;
}

.testimonials_container {
  text-align: left;
  padding: 0em;
}

.testimonials_container:hover {
  cursor: grab;
}

.testimonials_list {
  padding: 1em 0.2em 3em 0.2em;
  margin: 1em 0em;
  border-radius: 10px;
  background-color: rgb(246, 246, 246);
}

.testimonials_list div {
  display: flex;
  padding: 1em;
}

.testimonials_list div span p {
  color: green;
}

.reviewDescription {
  margin: -0.5em 1em;
}

.Blogs {
  background-color: white;
  text-align: center;
  padding-top: 2em;
}

.Blogs h2 {
  font-size: xxx-large;
}

.blogsList {
  background-color: rgb(246, 246, 246);
  border-radius: 10px;
  margin: 1em;
  transition: 0.4s;
}

.blogsList:hover {
  cursor: pointer;
  transform: scale(1.009);
  transition: 0.4s;
  box-shadow: grey 2px 2px 20px;
}

.blogsList img {
  width: 100%;
  border-radius: 10px 10px 0px 0px;
}

.blogsList h3 {
  margin: 1em;
  text-align: left;
  transition: 0.4s;
}

.blogsList h3:hover {
  color: green;
  transition: 0.4s;
}

.Link {
  text-decoration: none;
  color: black;
}

.blogsList p {
  margin: 1em;
  text-align: left;
}

.blogInfo {
  display: flex;
  text-align: left;
  font-weight: 700;
  padding-bottom: 2em;
}

.Footer {
  padding: 0.5em;
  background-color: whitesmoke;
  display: flex;
  flex-direction: column;
}

.footer-columns {
  margin: 0.5em 2em;
}

.footer-social-icons>a>img {
  height: 50px;
  width: 50px;
  margin: 0.5em;
}

.footerLogo {
  height: 60px;
  width: 60px;
}

.exploreLinks,
.linksCol,
.questionsCol {
  display: flex;
  flex-direction: column;
}

.exploreLinks>a {
  margin-top: 0.5em;
  text-decoration: none;
  color: black;
  transition: 0.7s;
}

.exploreLinks>a:hover {
  color: green;
  transition: 0.7s;
  scale: 1.03;
}

.linksCol>a {
  margin-top: 0.5em;
  text-decoration: none;
  color: black;
  transition: 0.7s;
}

.linksCol>a:hover {
  color: green;
  transition: 0.7s;
  scale: 1.03;
}

.questionsCol>a {
  margin-top: 0.5em;
  text-decoration: none;
  color: black;
  transition: 0.7s;
}

.questionsCol>a:hover {
  color: green;
  transition: 0.7s;
  scale: 1.03;
}

.footer-social-icons>a>img:hover {
  cursor: pointer;
  transition: 0.4s;
  transform: scale(1.2);
}

.backgroundWhite {
  padding-top: 1em;
}

.Upload {
  background-color: white;
  width: 100%;
  display: flex;
  justify-content: center;
}

.Upload form {
  position: relative;
  width: 87vw;
  z-index: 1;
  background-color: white;
  border-radius: 10px;
  padding: 2em .5em;
  margin: 3em 0em;
  box-shadow: 2px 2px 20px grey;

}

.Upload>form>label {
  color: green;
  font-weight: 500;
  font-size: larger;
  margin: 0.5em 1em;
}

.Upload>form>input {
  margin: 0.5em 1em;
  padding: 0.5em 0.5em 0 0;
  font-size: larger;
  width: 75vw;
  border: none;
  border-bottom: green solid 2px;
}

.Upload>form>input:focus {
  outline: none;
}

.Upload>form>a {
  padding: .7em 2em;
  font-weight: 600;
  margin: .4em 1em;
  font-size: large;
  background-color: white;
  border: grey 2px solid;
  text-decoration: none;
  color: black;
  border-radius: 5px;
  transition: 0.5s;

}

.Upload>form>a:hover {
  border: none;
  background-color: green;
  box-shadow: 2px 2px 20px grey;
  color: white;
  transition: 0.5s;
  cursor: pointer;
  border: grey solid 2px;
}

.Upload>form>button {
  padding: 1em 2em;
  margin: 1em;
  font-weight: 600;
  font-size: large;
  border: none;
  width: max-content;
  border-radius: 5px;
  transition: 0.5s;
}

.Upload>form>button:hover {
  border: none;
  background-color: green;
  box-shadow: 2px 2px 20px grey;
  color: white;
  transition: 0.5s;
  cursor: pointer;
}

